import { Component, OnInit, OnChanges, SimpleChanges, SimpleChange, Output, EventEmitter, HostListener } from '@angular/core';
import { MatToolbarModule, MatButtonModule, MatCardModule, MatInputModule } from '@angular/material';
import { YoutubeApiService } from './../youtube-api.service';

@Component({
  selector: 'app-content',
  templateUrl: './content.component.html',
  styleUrls: ['../app.component.css']
})
export class ContentComponent implements OnChanges {

  infoApi: string;
  videosInfo: Array<any>;
  resultsPerPage: any;
  hearts: boolean[];

  showSpinner = false;

  textSearch = '';

  constructor(private yt: YoutubeApiService) {
  }

  fetchVideos() {
    this.yt.searchVideos((this.textSearch))
      .then((json: any) => {
        this.infoApi = json;

        this.videosInfo = this.infoApi['items'];
      })
      .catch(() => {
        console.log('request error');
      });
  }

  likeVideo(video) {
    if (video.liked != null) {
      video.liked = !video.liked;
    } else {
      video.liked = true;
    }
  }

  deleteItem(index) {
    this.videosInfo.splice(index, 1);
  }

  receiveText(e) {
    this.textSearch = e.target.value;
  }

  ngOnChanges(changes: SimpleChanges) {
    const textSearch: SimpleChange = changes.textSearch;

    this.fetchVideos();
  }
}



