import { Component } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import {
  MatToolbarModule, MatMenuModule, MatCardModule, MatButtonModule, MatInputModule,
  MatProgressSpinnerModule, MatSnackBarModule, MatIconModule
} from '@angular/material';

import { FormsModule } from '@angular/forms';

@NgModule({
  imports: [BrowserAnimationsModule, MatToolbarModule, MatMenuModule, MatCardModule, MatButtonModule, MatInputModule,
    MatProgressSpinnerModule, FormsModule, MatSnackBarModule, MatIconModule],
  exports: [BrowserAnimationsModule, MatToolbarModule, MatMenuModule, MatCardModule, MatButtonModule, MatInputModule,
    MatProgressSpinnerModule, FormsModule, MatSnackBarModule, MatIconModule]
})

export class MaterialModule {}



