import 'rxjs/add/operator/map';
import { Injectable } from '@angular/core';
import { Response } from '@angular/http';
import { Http } from '@angular/http';

@Injectable()
export class YoutubeApiService {

  imagesPath: string;
  currentText: string;
  apiKey = 'AIzaSyDQ4Sc6AsHF5ai-wHlEV2YV8unurZmJkQ8';

  constructor(public http: Http) { }

  // searchVideos(textSearch) {
  //   return this.http.get('https://www.googleapis.com/youtube/v3/search?key=' + this.apiKey +
  //                         '&q=' + textSearch + '&part=snippet,id&maxResults=20')
  //     .map((res: Response) => res.json());
  // }

  searchVideos(textSearch): Promise<any[]> {
    return new Promise((resolve, reject) => {
      resolve(this.http.get('https://www.googleapis.com/youtube/v3/search?key=' + this.apiKey +
        '&q=' + textSearch + '&part=snippet,id&maxResults=20')
        .map((res: Response) => res.json())
        .toPromise());
    });
  }

  setCurrentText(text: string): void {
    this.currentText = text;
  }

  getCurrentText(): string {
    return this.currentText;
  }
}





